<template>
    <div>
        <div class="header">
            <div class="headerMain">
                <div class="Notice">
                </div>
                <div class="Account">
                    <div class="Left">
                        <div class="Info">
                            <font>{{ userName }}</font>
                            <input type="button">
                            <div id="InfoPopup">
                                <ul>
                                    <li><a @click="logOut()">退出</a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="navTop">
            <div class="navTopmain">
                <div class="logo">
                <a href="/">
                    <img style="height: 50px;width: 50px;margin-top: 1px;" src="@/assets/MYHD.png"/>
                    <font style="font-size: 2em;color: white; font-family: 'Courier New', Courier, monospace;font-weight: bolder;">MYHD人才招聘平台</font>
                </a>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Cookies from 'js-cookie';
export default {
    data () {
        return {
            userName:"",//用户名
        }
    },
    mounted() {
        this.userName = Cookies.get('cookieUserName');
    },
    methods: {
        logOut() {
            Cookies.remove("cookieUserId")
            Cookies.remove("cookieUserEmail")
            Cookies.remove("cookieUserName")
            Cookies.remove("cookiePhone")
            window.location.href="/"
        }
    },
}
</script>